body{
  background-color:#5daff3;
}


.App{
  background-color:#095d8d;
  min-height:500px;
  width:400px;
  margin: 40px auto;
}
h1{
  color:#fff;
  font-size:24px;
  margin:10px auto;
}

#todo-form input{
  background-color: #093955;
  height:50px;
  width:200px;
  border:0px;
  padding: 0 20px;
  margin: 20px;
  border-radius:10px;
  font-size: 18px;
  color:white;
}

#todo-form input::placeholder{
 color:#e0eef7;
}

#todo-form button{
  background-color: #3bdbce;
  height:50px;
  width:80px;
  border:0px;
  padding: 0 20px;
  cursor:pointer;
  border-radius:5px;
  font-size: 18px;
  font-weight: bolder;
  color:white;
}
#todo-form input:focus,#todo-form button:focus{
  outline: none;
}

.list{
  background-color:orange;
  width:300px;
  height:50px;
  color:white;
  border-radius:5px;
  margin: 20px auto;
}

.list p{
  padding: 10px;
  position:relative;
}

.list p span{
  right:10px;
  position:absolute;
  cursor:pointer;
}

.faicons{
  font-size:14px;
  margin:0 5px;
}

.list p input{
  border:0;
  color:#fff;
  background-color:transparent;
  font-size:18px;
}
.list p input:focus{
  outline:none;
}

